import React, { PureComponent } from 'react'
import { connect } from 'react-redux'
import { HeaderWrapper } from '../style'

class MyHeader extends PureComponent {
  render() {
    return (
      <HeaderWrapper>
        <div className='head'>
        </div>
        <div className='contact'>
          <p className='contact-info'><i className='iconfont'>&#xe7aa;</i>{this.props.info.EMail}</p>
          <p className='contact-info'><i className='iconfont'>&#xe617;</i>{this.props.info.wx}</p>
          <p className='contact-info'><i className='iconfont'>&#xe780;</i>{this.props.info.github}</p>
          <p className='contact-info'><i className='iconfont'>&#xe665;</i>{this.props.info.githubPage}</p>
          <p className='contact-info'><i className='iconfont'>&#xe62c;</i>{this.props.info.phone}</p>
        </div>
        <div className='name'>
          <h1 className='name-myname'>{this.props.info.name}</h1>
          <p className='name-job'>{this.props.info.job}</p>
        </div>
      </HeaderWrapper>
    )
  }
}

const mapState = state => ({
  info: state.resume.info
})

export default connect(mapState, null)(MyHeader)
